import alertBox from '@/components/color-ui-dialog/color-ui-dialog.vue'
import zTable from "@/components/z-table/z-table.vue"
import uniIcon from "@/components/uni-icon/uni-icon.vue"
import searchForm from '@/components/searchForm/searchDataList.vue.vue'
//纸板入库卡板、工单扫描
import baseMixin from '@/mixins'
import {mapActions} from 'vuex'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
const defaultformItem={
				cardBoardNo:'',//卡板号
				kuNo:'',//库位号
				originalKuNo:'' //原库位
			}
export default {
	name:'inStorage',//纸板入库卡板、工单扫描
	mixins:[baseMixin],
	components:{zTable,searchForm,uniPopup,uniIcon,alertBox},
	data() {
		return {
			toShowModal:false,// 是否显示确认弹框
			msgContent:'',//弹框内容信息
			scanType:'',//当前扫描类型
			tableHeight:400,//表格高度
			errorContent:'暂无数据',//数据加载中...
			cardBoardNoInfoData:[],
			cardBoardNoInfoColumns: [{
				key: 'ap_OrderNo',
				title: '工单号',
				width: 200
			}, 
			{
				key: 'ap_ArtID',
				title: '纸质',
				width: 200
			}, 
			{
				key: 'ap_Size',
				title: '规格',
				width: 200
			}, 
			{
				key: 'ap_Qty',
				title: '数量',
				width: 145
			},
			{
				key: 'ap_CustName',
				title: '客户',
				width: 145
			}],
			formItem:Object.assign({},defaultformItem)
		}
	},








	onReady () {
		this.messageRegister()
		this.getTableHeight()
	},

	methods: {
		//查询卡板信息列表BY 卡板号
						getCardBoardNoInfo() {
							this.cardBoardNoInfoData= []
							if (this.formItem.cardBoardNo == '') {
								uni.showToast({
									title:'请输入或扫描卡板号',
									icon:'none',
									duration:2000
								})
								return;
							}
							let data = {
								ap_CardNo: this.formItem.cardBoardNo
							}
							
							this.dataIsLoadding = true;
							this.setCardBoardNorwayAction(data).then(res => {
								//debugger
								if (res && res.length > 0) {
									this.cardBoardNoInfoData = res
									this.formItem.originalKuNo=this.cardBoardNoInfoData[0].ap_StationNo
								}else{
									uni.showToast({
										title:'没有该卡板对应的数据',
										icon:'none',
										duration:2000
									})
								}
								this.dataIsLoadding = false;
								
							}).catch(err => {
								uni.showToast({
									title:'获取数据失败:' + err,
									icon:'none',
									duration:2000
								})
								this.dataIsLoadding = false;
							})
						},
		//确认弹框-提交工单列表信息
		comfirmModal(val){
			this.toShowModal =false
			switch (this.currentAction){
				case 'preSubmitDataList':
				     this.submitAddList()
					break;			
				default:
					break;
			}
		},
		// 取消弹框
		cancelModal(val){
			this.toShowModal =false
			// switch (this.currentAction){
			// 	case 'cardBoardNotNull':
			// 	    this.formItem.cardBoardNo =this.tempScanCode
			// 	    this.getCardBoardNoInfo()
			// 		break;			
			// 	default:
			// 		break;
			// }
		},
		// 准备--提交提交工单列表信息
		preSubmitDataList(){
			this.currentAction='preSubmitDataList'
			this.msgContent ='确定提交数据吗?'
			this.toShowModal =true
		},
		// 提交添加卡板工单信息列表
		submitAddList(){
		  let params = {
			   newStationNo:this.formItem.kuNo,
			   StationNo:this.cardBoardNoInfoData[0].ap_StationNo
		  }
		  this.$store.dispatch('setStationNoAction',params).then(res=>{
			  // 清空数据
			  this.formItem = Object.assign({},defalutformItem)
			  this.cardBoardNoInfoData =[]
			  uni.showToast({
			  	title:'操作成功',
			  	icon:'none',
			  	duration:2000
			  })
		  }).catch(err=>{
				uni.showToast({
					title:'操作失败:'+err,
					icon:'none',
					duration:2000
				})
		  })
		},
		async getTableHeight(){
					 // debugger
					  let _self=this
					  let tempHeight =  _self.setTableHeight()
					  let otherHeight= await _self.getOtherContentHeight("elForm") 
					  let otherHeight2 = await _self.getOtherContentHeight("elBtn")
					  if(otherHeight!=null && otherHeight2!=null){
					  	
					  	_self.tableHeight =tempHeight-otherHeight-otherHeight2-20
					  }
				
					},
			//映射方法
		...mapActions(['setCardBoardNorwayAction']),
			//页面通讯,事件注册
			messageRegister(){
			  // 初始化数据
			},

		   //===打开扫描===
			turnOnScanCode(type){
				this.scanType =type
				let _self =this
				// 调起条码扫描
				uni.scanCode({
				    scanType: 'barCode', // 固定条形码
				    success: function (res) {
				        console.log('条码内容：' + res.result);
						switch (type){
							case 'cardBoardNo': 
							    //卡板号
							    this.formItem.cardBoardNo = res.result
								_self.getCardBoardNoInfo() //工单号验证查询
								break;
							case 'kuNo': 
								    //库位号
								    this.formItem.kuNo = res.result
									//_self.getWorkNoInfo() //工单号验证查询
									break;
							default:
								break;
						}
					
				    }
				});
			},
		
	}
}
